'use strict';

/*
 This Service will hold the chart configuration for D3 charts
 */

angular
    .module('tuktukV2Dahboard')
    .factory('ChartConfigService', ChartConfigService)

function ChartConfigService($q, $resource, API, PerformanceHandler) {
    var factory = {}
    Date.prototype.addHours = function (h) {
        this.setHours(this.getHours() + h);
        return this;
    }
    factory.lineChartConfig = {refreshDataOnly: false}
    factory.lineChartOptions = {
        chart: {
            type: 'lineChart',
            height: '400',
            zoom: {
                enabled: true
            },
            interpolate: 'basis',
            legend: {
                vers: 'classic',
                maxKeyLength: 400,
                margin: {
                    top: 5,
                    right: 0,
                    bottom: 30,
                    left: 0
                }
            },
            legendPosition: 'top',
            margin: {
                top: 0,
                right: 20,
                bottom: 100,
                left: 50
            },
            x: function (d) {
                return d[0];
            },
            //  y: function(d){ return d[1]/100; },
            y: function (d) {
                return d[1];
            },
            //   average: function(d) { return d.mean/100; },

            color: d3.scale.category10().range(),
            duration: 300,
            useInteractiveGuideline: true,
            clipVoronoi: false,
            showYAxis: true,
            xAxis: {
                tickSize: '10',
                // axisLabel: 'Per Hour',
                rotateLabels: '-90',
                tickFormat: function (d) {
                    //   return d3.time.format('%d %B %y')(new Date(d))
                    return d3.time.format('%d %b %I %p')(new Date(d))
                },

                showMaxMin: true,
                staggerLabels: false,
                axisLabelDistance: 0
            },
            x2Axis: {
                axisLabel: 'Date',
                tickFormat: function (d) {
                    //   return d3.time.format('%d %B %y')(new Date(d))
                    return d3.time.format('%I %p')(new Date(d))
                },

                showMaxMin: true,
                staggerLabels: false
            },

            yAxis: {
                axisLabel: 'Trips',
                tickFormat: function (d) {
                    return d3.round(d);
                    // return d3.format(',.1%')(d);
                },
                showMaxMin: true,
                axisLabelDistance: 200
            }
        }
    };
    factory.discreteBarChartOptions = {
        chart: {
            type: 'discreteBarChart',
            height: 450,
            zoom: {
                enabled: true
            },
            legend: {
                vers: 'classic',
                maxKeyLength: 400,
                margin: {
                    top: 5,
                    right: 0,
                    bottom: 30,
                    left: 0
                }
            },
            legendPosition: 'top',
            margin: {
                top: 30,
                right: 50,
                bottom: 100,
                left: 55
            },
            x: function (d) {
                return d.label;
            },
            //    y: function(d){return d.value + (1e-10);},
            y: function (d) {
                return d.value
            },
            showValues: true,
            valueFormat: function (d) {
                return d3.round(d);
                //  return d3.format(',.4f')(d);
            },
            duration: 500,
            xAxis: {
                axisLabel: '',
                rotateLabels: '-90'
            },
            yAxis: {
                axisLabel: '',
                axisLabelDistance: -10
            }
        }
    }
    factory.multiBarChartOptions = {
        chart: {
            type: 'multiBarChart',
            height: 400,
            margin: {
                top: 30,
                right: 50,
                bottom: 100,
                left: 55
            },
            legend: {
                vers: 'classic',
                maxKeyLength: 400,
                margin: {
                    top: 5,
                    right: 0,
                    bottom: 30,
                    left: 0
                }
            },
            legendPosition: 'top',
            text: 'Credit Recovery',
            x: function (d) {
                return d.x;
            },
            y: function (d) {
                return d.y;
            },
            stacked: true,
            showControls:false,
            clipEdge: true,
            transitionDuration: 1000,
            useInteractiveGuideline: false,
            //   xScale : d3.time.scale(), // <-- explicitly set time scale
            xAxis: {
                rotateLabels: '-90',
                ticks: d3.time.months, // <-- add formatter for the ticks
                tickFormat: function (d) {
                    //return d3.time.format('%d-%m-%Y')(new Date(d))
                    //return d3.time.format('%I %p')(new Date(d))
                    return d3.time.format('%d %b %y')(new Date(d));
                },
                showMaxMin: false
            },

            yAxis: {
                tickFormat: function (d) {
                    return d;
                }
            },

        }

    };
    factory.linePlusBarChartOptions = {
        chart: {
            type: 'linePlusBarChart',
            height: 400,
            focusEnable:false,
            legend: {
                vers: 'classic',
                maxKeyLength: 400,
                margin: {
                    top: 5,
                    right: 100,
                    bottom: 30,
                    left: 0
                }
            },
            legendPosition: 'left',
            margin: {
                top: 30,
                right: 75,
                bottom: 100,
                left: 75
            },
            bars: {
                forceY: [0]
            },
            color: ['#ff7f0e', '#e377c2'],
            x: function(d,i) { return i },
            xAxis: {
                rotateLabels: '-90',
                axisLabel: '',
                tickFormat: function(d) {
                    var dx = $scope.data[0].values[d] && $scope.data[0].values[d].x || 0;
                    if (dx > 0) {
                        return d3.time.format('%x')(new Date(dx))
                    }
                    return null;
                }
            }

        }
    };

    return factory;
}

(function () {
    'use strict';

    angular
        .module('tuktukV2Dahboard')
        .run(runBlock);

    /** @ngInject */
    function runBlock($log, UserService, StaticDataService, $http, toastr, $state, $rootScope, LoginService, $templateCache) {
        if (UserService.getUser() != null) {
            $http.defaults.headers.common.Authorization = 'Basic ' + UserService.getUser().token;
        }
        $log.debug('runBlock end');
        $rootScope.city = StaticDataService.cities[0].value
        $rootScope.vehicleType = StaticDataService.vehicleTypes[0].value
        /* $rootScope.$on('$viewContentLoaded', function() {
             var currentPageTemplate = $state.current.templateUrl;
             $templateCache.remove(currentPageTemplate);
             $state.reload();
         });*/
        $rootScope.logout = function () {
            LoginService.logout({}, function (response) {
                    toastr.error("You have logged out successfully.");
                },
                function (error) {
                    toastr.error(error);
                })
            UserService.clearUser()
            $state.go("login")
            $rootScope.isAuthenticated = false;
        }
        $rootScope.$on('unauthorized', function() {
            UserService.clearUser()
            $state.go("login")
            $rootScope.isAuthenticated = false;
        });
        $rootScope.$on('$stateChangeStart', function (e, toState, toParams, fromState, fromParams) {
            console.log("to - " + toState.name + " $state.current.name - " + $state.current.name)
            if (UserService.getUser() != null && (toState.name == "login")) {
                $rootScope.isAuthenticated = true;
                $state.go("home.live" ,{city:  $rootScope.city , vehicleType:$rootScope.vehicleType})
                e.preventDefault();
                //   returna

            }
        });
    }

})();

(function () {
    'use strict';

    angular
        .module('tuktukV2Dahboard')
        .config(routerConfig);

    /** @ngInject */
    function routerConfig($stateProvider, $urlRouterProvider) {

        $urlRouterProvider.otherwise('/login');
        $stateProvider
            .state('login', {
                url: '/login',
                templateUrl: 'app/login/login.html',
                controller: 'LoginController',
                controllerAs: 'vm'
            })
            .state('home', {
                url: '/home',
                templateUrl: 'app/main/main.html',
                controller: 'MainController',
                controllerAs: 'main',
                abstract: true
            })
            .state('home.performance', {
                url: '/performance/:city/:vehicleType',
                templateUrl: 'app/performance/performance.html',
                controller: 'PerformanceController',
                controllerAs: 'vm'
            })
            .state('home.maps', {
                url: '/maps/:city/:vehicleType',
                templateUrl: 'app/maps/maps.html',
                controller: 'MapsController',
                controllerAs: 'vm',
                cache: false
            })
            .state('home.live', {
                    url: '/live/:city/:vehicleType',
                    templateUrl: 'app/live/live.html',
                    controller: 'LiveController',
                    controllerAs: 'vm',
                    /*resolve: {
                        heatmapResolve: function (LiveService) {
                            LiveService.heatmap({
                                city: $rootScope.city,
                                vehicle: $rootScope.vehicleType,
                                from: from,
                                to: to,
                                state: vm.selected.id
                            })
                    }}*/
                })
            .state('home.drivers', {
                    url: '/drivers/:city/:vehicleType',
                    templateUrl: 'app/drivers/drivers.html',
                    controller: 'DriversController',
                    controllerAs: 'vm'
                })
            .state('home.offers', {
                url: '/offers/',
                templateUrl: 'app/offers/offers.html',
                controller: 'OffersController',
                controllerAs: 'vm'
            })
            .state('home.drivers_detail', {
                url: '/detail/:driverId',
                templateUrl: 'app/drivers/detail.html',
                controller: 'DriversDetailController',
                controllerAs: 'vm'
            })
            .state('home.trips', {
                url: '/trips',
                templateUrl: 'app/trips/trips.html',
                controller: 'TripsController',
                controllerAs: 'vm'
            })
            .state('home.trip_detail', {
                    url: '/tripdetail/:id/:driverId/:riderId',
                    templateUrl: 'app/trips/trip_details.html',
                    controller: 'TripDetailController',
                    controllerAs: 'vm'
                })
            .state('home.riders', {
                url: '/riders',
                templateUrl: 'app/riders/riders.html',
                controller: 'RidersController',
                controllerAs: 'vm'
            })
            .state('home.surge', {
                url: '/surge',
                templateUrl: 'app/surge/surge.html',
                controller: 'SurgeController',
                controllerAs: 'vm'
            })
            .state('home.alert', {
                url: '/alert',
                templateUrl: 'app/alert/alert.html',
                controller: 'AlertController',
                controllerAs: 'vm'
            })
    }

})();

